<template>
  <div class="Dnzl">
    <span class="demonstration"> 选择周期:</span>
        <!-- 图表内容 label不设置则等同于value的值-->
    <el-select @change="changeCycle()" v-model="cycleValue" placeholder="选择周期" >
      <el-option
        v-for="item in cycleOptions"
        :key="item.value"
        :value="item.value">
      </el-option>
    </el-select>


    <span class="demonstration">时间:</span>
    <!-- 日期选择器 -->
    <el-date-picker
    :style="timeChoose1"
    value-format="yyyy-MM"
    v-model="TimeValueMonth"
    type="month"
    placeholder="选择月份">
    </el-date-picker>

    <el-date-picker
    :style="timeChoose2"
    value-format="yyyy"
    v-model="TimeValueYear"
    type="year"
    placeholder="选择年份">
    </el-date-picker>


    <el-button type="primary" @click="showTable()">查询</el-button>

    <div>
      <el-table border :data="tableData" :header-cell-style="{textAlign: 'center'}">
        <el-table-column v-for="(item, index) in transTitle" :label="item" :key="index" align="center" width="120">
          <template slot-scope="scope">
            {{scope.row[index]}}
          </template>
        </el-table-column>
      </el-table>

    </div>

  </div>
</template>


<script>
import axios from 'axios'
export default {
  data(){
    return{
      TimeValue:null,
      TimeValueMonth:null,
      TimeValueYear:null,

      timeChoose1:'',
      timeChoose2:'display:none',

      cycleValue:'按月',

      cycleOptions:[{
        value:'按月'
      },{
        value:'按年'
      }],

      //横向表格
      originData: [],//存放原始数据
      originTitle: [],//存放指标，指标固定，赋值在初始化函数中
      transTitle:['指标'],//存放一年12个月或者一月31天

      tableData:[],

      tableDataNow:[],
      tableDataMonth:[],
      tableDataYear:[],


    }
  },
  mounted(){
    this.convert()
    
  },

  

  methods:{
    changeCycle(){
      if(this.cycleValue == '按月'){
        this.timeChoose1='',
        this.timeChoose2='display:none'

        this.TimeValue = this.TimeValueMonth//时间赋值

        this.tableDataNow = this.tableDataMonth
        
      }

      if(this.cycleValue == '按年'){
        this.timeChoose1='display:none',
        this.timeChoose2=''

        this.TimeValue = this.TimeValueYear//时间赋值

        this.tableDataNow = this.tableDataYear
      }
    },


    initTable(){//初始化表格
      this.originData=[]
      this.originTitle=['三相电流不平衡率越上限',
                        '三相电压不平衡率越上限',
                        '频率越上限',
                        'A相谐波电流畸变率越上限',
                        'B相谐波电流畸变率越上限',
                        'C相谐波电流畸变率越上限',
                        'A相谐波电压畸变率越上限',
                        'B相谐波电压畸变率越上限',
                        'C相谐波电压畸变率越上限',]
      this.transTitle=['指标']//天数赋值
      
      if(this.cycleValue == '按月'){
        let i=1
        while(i<=31){
          this.transTitle[i]=i+''
          this.originData.push({
            sxI:'',
            sxU:'',
            f:'',
            ia:'',
            ib:'',
            ic:'',
            ua:'',
            ub:'',
            uc:'',
          })
          i++
        }

      }
      else if(this.cycleValue == '按年'){
        let i=1
        while(i<=12){
          this.transTitle[i]=i+''
          this.originData.push({
            sxI:'',
            sxU:'',
            f:'',
            ia:'',
            ib:'',
            ic:'',
            ua:'',
            ub:'',
            uc:'',
          })
          i++
        }
      }
    },
    showTable(){
      this.changeCycle()
      this.initTable()
      if(this.TimeValue != null){
        let i=0
        while(i<this.tableDataNow.length){
          this.originData[i].sxI = this.tableDataNow[i].iUnbalance
          this.originData[i].sxU = this.tableDataNow[i].uUnbalance
          this.originData[i].f = this.tableDataNow[i].frequencyDeviation
          this.originData[i].ia =  this.tableDataNow[i].iaPhd
          this.originData[i].ib =  this.tableDataNow[i].ibPhd
          this.originData[i].ic =  this.tableDataNow[i].icPhd
          this.originData[i].ua =  this.tableDataNow[i].uaPhd
          this.originData[i].ub =  this.tableDataNow[i].ubPhd
          this.originData[i].uc =  this.tableDataNow[i].ucPhd

          i++
        }

      }
      
      //获取数据之后，进行矩阵转化.
      let matrixData = this.originData.map((row) => {
        let arr = []
        for (let key in row) {
          arr.push(row[key])
        }
        return arr
      })
    
      this.tableData = matrixData[0].map((col, i) => {
        return [this.originTitle[i], ...matrixData.map((row) => {
          return row[i]
        })]
      })
      
    },

    convert(){
      axios
      .post("http://121.43.111.195/PowerSafe/findMonthByDataTime", {
	      deviceId:"1",
	      dataTime:"1638288000"
      })
      .then(response => {
        this.tableDataMonth = response.data
        
        console.log(this.tableDataMonth);
        this.showTable()
      })
      .catch(error => {
        console.log(error);
      });

      axios
      .post("http://121.43.111.195/PowerSafe/findYearByDataTime  ", {
	      deviceId:"1",
	      dataTime:"1609430400"
      })
      .then(response => {
        this.tableDataYear = response.data
        
        console.log(this.tableDataYear);
        this.showTable()
      })
      .catch(error => {
        console.log(error);
      });

      


    }
  }

  
}
</script>
